<template>
  <div>
    <loading :active.sync="isLoading">
      <template slot="default">
        <img src="../../assets/img/loading.svg" alt="" />
      </template>
    </loading>
    <div class="productdetails container">
      <nav aria-label="breadcrumb">
        <ol class="breadcrumb bg-white mb-0 mt-3">
          <li class="breadcrumb-item">
            <router-link to="/" class="text-funOrange">首頁</router-link>
          </li>
          <li class="breadcrumb-item">
            <router-link
              :to="{
                path: '/product',
                query: { productCategory: '' },
              }"
              class="text-funOrange"
              >商品</router-link
            >
          </li>
          <li class="breadcrumb-item active" aria-current="page">
            {{ product.title }}
          </li>
        </ol>
      </nav>
      <div class="row mb-5">
        <div class="col-lg-6 mb-5">
          <div class="rounded shadow h-100 p-3">
            <div
              style="height: 200px; background-repeat:no-repeat; background-position: center"
              :style="{ backgroundImage: `url(${product.imageUrl})` }"
              class="border-bottom mb-3"
            ></div>
            <div class="description ">
              <h3 class="text-funDarkOrange h5">【商品規格】</h3>
              <p>{{ product.description }}</p>
            </div>
            <div class="content">
              <h3 class="text-funDarkOrange h5">【商品詳情】</h3>
              <p>{{ product.content }}</p>
            </div>
          </div>
        </div>
        <div class="col-lg-6 mb-5">
          <div class="rounded shadow p-3">
            <div
              class="title border-bottom d-flex justify-content-between mb-3"
            >
              <h3 class="font-weight-bold">{{ product.title }}</h3>
              <p class="badge badge-secondary p-2">{{ product.category }}</p>
            </div>
            <div class="price border-bottom mb-3">
              <div
                v-if="product.price == product.origin_price"
                class="h5 text-funOrange font-weight-bold"
              >
                NT${{ product.origin_price }}
              </div>
              <del
                v-if="product.price !== product.origin_price"
                class="h6 text-funOrange"
              >
                原價NT${{ product.origin_price }}</del
              >
              <div
                v-if="product.price !== product.origin_price"
                class="h4 text-funDarkOrange font-weight-bold"
              >
                特價NT${{ product.price }}
              </div>
              <select name="" class="form-control my-3" v-model="product.num">
                <option :value="num" v-for="num in 10" :key="num">
                  選購 {{ num }} {{ product.unit }}
                </option>
              </select>
              <button
                class="btn btn-funDarkOrange w-100 mb-3"
                @click="addtoCart(product.id, product.num)"
              >
                加到購物車
              </button>
            </div>
            <div class="deliverynote">
              <h3 class="text-funDarkOrange h5">【配送及相關說明】</h3>
              <p>
                線上購物的消費者，都可以依照消費者保護法的規定，享有商品貨到次日起七天猶豫期的權益。但猶豫期並非試用期，請留意，您所退回的商品必須回復原狀。
              </p>
              <dl class="row">
                <dt class="col-sm-3">寄送時間 :</dt>
                <dd class="col-sm-9">
                  全島24h到貨，遲到提供100元現金積點。全年無休，週末假日照常出貨。
                </dd>
                <dt class="col-sm-3">送貨方式 :</dt>
                <dd class="col-sm-9">
                  透過宅配送達。除網頁另有特別標示外，均為常溫配送。
                  消費者訂購之商品若經配送兩次無法送達，再經本公司以電話與Email均無法聯繫逾三天者，本公司將取消該筆訂單，並且全額退款。
                </dd>
                <dt class="col-sm-3">送貨範圍 :</dt>
                <dd class="col-sm-9">
                  限台灣本島與離島地區註，部分離島地區包括連江馬祖、綠島、蘭嶼、琉球鄉…等貨件，將送至到岸船公司碼頭，需請收貨人自行至碼頭取貨。注意！收件地址請勿為郵政信箱。
                </dd>
                <dt class="col-sm-3">售後服務 :</dt>
                <dd class="col-sm-9">
                  非人為損壞，均享一年保固。
                </dd>
              </dl>
            </div>
          </div>
        </div>
      </div>
      <!--relatedproducts-->
      <div class="relatedproducts container px-0 mb-5">
        <h3 class="h4 font-weight-bolder mb-4">你可能會喜歡</h3>
        <Slick :options="relatedproductsOptions" v-if="relatedproducts.length">
          <div v-for="item in relatedproducts" :key="item.id">
            <div class="card shadow-sm m-2">
              <div
                style="height: 180px; background-repeat:no-repeat; background-position: center"
                :style="{ backgroundImage: `url(${item.imageUrl})` }"
              ></div>
              <div class="card-body">
                <h5 class="card-title">
                  {{ item.title }}
                </h5>
                <div
                  class="d-flex justify-content-between align-items-baseline"
                >
                  <div
                    class="h4 text-funOrange"
                    v-if="item.price == item.origin_price"
                  >
                    ${{ item.origin_price }} 元
                  </div>
                  <del
                    class="h6 text-funOrange"
                    v-if="item.price !== item.origin_price"
                    >原價 {{ item.origin_price }} 元</del
                  >
                  <div
                    class="h4 text-funDarkOrange"
                    v-if="item.price !== item.origin_price"
                  >
                    ${{ item.price }} 元
                  </div>
                </div>
              </div>
              <div class="card-footer d-flex">
                <button
                  type="button"
                  class="btn btn-sm btn-funOrange w-100 text-light "
                  @click="toProduct(item.id)"
                >
                  馬上看看
                </button>
              </div>
            </div>
          </div>
        </Slick>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Slick from "vue-slick";

export default {
  inject: ["reload"],
  components: {
    Slick,
  },
  data() {
    return {
      product: {},
      isLoading: false,
      relatedproducts: [],
      relatedproductsOptions: {
        slidesToShow: 4,
        arrows: false,
        dots: false,
        autoplay: false,
        responsive: [
          {
            breakpoint: 1000,
            settings: {
              slidesToShow: 2,
              centerMode: true,
            },
          },
          {
            breakpoint: 768,
            settings: {
              slidesToShow: 2,
            },
          },
          {
            breakpoint: 430,
            settings: {
              slidesToShow: 1,
              centerMode: true,
            },
          },
        ],
      },
    };
  },
  watch: {
    $route() {
      this.reload(); //相關產品上一頁重整
    },
  },
  methods: {
    getProduct() {
      const vm = this;
      const id = this.$route.params.id;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${id}`;
      this.$http.get(url).then((response) => {
        vm.product = response.data.product;
        vm.product.num = 1;
      });
      this.getProducts();
    },
    getProducts() {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products/all`;
      this.$http.get(url).then((response) => {
        vm.relatedproducts = response.data.products.filter(
          (item) =>
            item.category == vm.product.category &&
            item.title !== vm.product.title
        );
      });
    },
    addtoCart(id, qty = 1) {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      const cart = {
        product_id: id,
        qty,
      };
      this.isLoading = true;
      this.$http.post(url, { data: cart }).then((response) => {
        vm.isLoading = false;
        vm.$bus.$emit("message:push", "加入成功", "funOrange");
      });
    },
    toProduct(id) {
      this.$router.push(`/product/${id}`);
      this.reload();
    },
  },
  created() {
    this.getProduct();
    this.getProducts();
  },
};
</script>

